:root {
  --color-bg-dark: #f5f5f5;
  --color-bg: #fdfffd;
  --color-gray: #d1d1d1;
  --color-text: #22243b;
  --color-accent: #f16e03;
  --color-white: white; 
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  background: rgb(81, 87, 111);
  background: linear-gradient(106deg,
      rgba(81, 87, 111, 1) 0%,
      rgba(60, 61, 69, 1) 100%);

}
* {
  box-sizing: border-box; /* padding, margin 요소들이 합해진 요소가 박스 사이즈가 되도록 */
}

#root {
  width: 100%;
  height: 60%;
  max-width: 500px;
  background-color: var(--color-bg-dark);
  overflow: hidden;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  box-shadow: 12px 13px 13px -1px rgba(0, 0, 0, 0.65);
  -webkit-box-shadow: 12px 13px 13px -1px rgba(0, 0, 0, 0.65);
  -moz-box-shadow: 12px 13px 13px -1px rgba(0, 0, 0, 0.65);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

ul {
  list-style: none;
  padding-left: 0;
}

button {
  outline: none;
  border: none;
}